<template>
	<div id="hardware"  >
		 <!-- <div class="hardware_head"></div> -->
		 <Menu class="menu"></Menu>
		 <div id="main">
		 	
		
			<div class="selector">
				<Selector  @select_attr="select_attr" :data="select_data"></Selector>	
			</div>
			<div class="card">
				<card  :data="list_data"></card>
				<div class="pagination">
					<Pagination :total='total' :current="current_num" :pageSize="page_size"  @num="Pagination_num"></Pagination>
				</div>
			</div>
			
		 </div>
		
		

		
		 
	</div>
</template>
<script>
	
	import Selector from  "@/components/Module/Selector"
	import Menu from "@/components/Module/Menu"
	import Pagination from "@/components/Module/Pagination"
	import card from "@/components/Module/card"
	export default{
		components:{
		    "card":card,
		    "Menu":Menu,
		    "Selector":Selector,
		    "Pagination":Pagination,
		    
		  },
		data(){
			return{
				goods_sort_id:1,/*id*/
				select_data:[],/**/
				total:0,/**/
				list_data:[],/**/

				select_list_attr:{"Brand":[],"series":[],"attr":[]},/**/

				current_num:1,
				page_size:10,
			}
		},
		watch:{
			$route(to,from){/*id*/

				if(to.name=="hardware"){

					if(to.query.id){
						this.initialization(to.query.id)
						
					}
					

				}else{
					
				}
			}
		},
		methods:{

			/*vuex*/
			initialization:function(id){/**/
				let Hardware_store=this.$store.state.Hardware;

				if(Hardware_store.goods_sort_id==id){
					/*console.log('',Hardware_store)*/
					let goods_sort_id=Hardware_store.goods_sort_id/*id*/
					let select_data=Hardware_store.select_data/**/
					let total=Hardware_store.total/**/
					let current_num=this.$store.state.Pagination.current_num/**/
					let list_data=Hardware_store.list_data[current_num]/**/

					this.goods_sort_id=goods_sort_id/*id*/
					this.select_data=select_data/**/
					this.total=total/**/
					this.list_data=list_data/**/

					this.current_num=current_num
					/*console.log("",current_num)*/

				}else{
					/*console.log('');*/
					(async ()=>{
							this.select_list_attr={"Brand":[],"series":[],"attr":[]};/**/
							/*this.$store.commit("set_PageNum_display",[]);*//**/
							this.$store.commit("Hardware_list_data_empty")/**/
							this.$store.commit("set_current_num",1)/**/
							this.$store.commit("selector_tag",[])/**/
							this.$store.commit("Hardware_gsid",id)/*id*/
							this.goods_sort_id=id;
							this.total=0;
							let select_data=await this.query_ApiSelect(this.goods_sort_id)/**/
							this.$store.commit("Hardware_select_data",select_data)/*vuex*/
							let total=await this.query_ApiPagination({"Brand":[],"series":[],"attr":[]})/**/
							this.$store.commit("Hardware_total",total)/*vuex*/
							let list_data=await this.query_list({/**/
								"goods_sort_id":this.goods_sort_id,
								"page_num":0,
								"page_size":this.page_size,
								"select_attr":{"Brand":[],"series":[],"attr":[]}
							})
							/*console.log(list_data)*/
							this.$store.commit("Hardware_list_data",{page_num:1,data:list_data})

							/*console.log(Hardware_store)		*/	
						
					})()
				}
			},

			/**/
			/**/
			select_attr:function(attr){/**/
				this.$store.commit("Hardware_list_data_empty")
				this.select_list_attr=attr;
				
				this.$store.commit("set_current_num",1);/**/
				this.total=1;

				(async ()=>{
					let total=await this.query_ApiPagination(attr)/**/
					this.total=total;
					this.current_num=1;
					this.$store.commit("Hardware_total",total)/*vuex*/
					let list_data=await this.query_list({
								"goods_sort_id":this.goods_sort_id,
								"page_num":0,
								"page_size":this.page_size,
								"select_attr":this.select_list_attr
							})
							/*console.log(list_data)*/
					this.$store.commit("Hardware_list_data",{page_num:1,data:list_data})
					
					/*console.log(this.$store.state.Hardware)*/
				})()

				


				
				/*console.log(this.list_data)*/

			},
			/**/
			Pagination_num:function(num){/**/
				/*console.log(num)*/
				this.$store.commit("set_current_num",num);/*vuex*/

				if(this.$store.state.Hardware.list_data[num]){
						/*console.log('')*/
						this.list_data=this.$store.state.Hardware.list_data[num]/*vuex*/
				}else{
					(async ()=>{
						let list_data=await this.query_list({
												"goods_sort_id":this.goods_sort_id,
												"page_num":num,
												"page_size":this.page_size,
												"select_attr":this.select_list_attr
											})
						this.$store.commit("Hardware_list_data",{page_num:num,data:list_data})
						/*console.log("",this.select_list_attr)
						console.log(this.$store.state.Hardware)*/

					})()
					
				}
				
				
			},
			/*vuex*/




			/**/

			query_ApiSelect:function(id){/**/
				this.$store.commit("loadChange",true)
				return new Promise((resolve)=>{
					this.$http({
							url:`${this.$store.state.query_host}/ApiSelect`,/**/
							method:'get',
							params:{
								id:this.goods_sort_id
							}

						}).then(data=>{
							if(data.status==200){/**/
								if(data.data.message==1){
									this.$store.commit("loadChange",false);
									this.$store.commit("set_mini_nav",false);
									this.select_data=data.data.select_data
									resolve(this.select_data)
								}
							}
						})
				})
				
				
						
			},
			query_ApiPagination:function(attr){/**/
				return new Promise((resolve)=>{
					this.$http({
						url:`${this.$store.state.query_host}/ApiPagination`,/**/
						method:'get',
						params:{
							goods_sort_id:this.goods_sort_id,
							select_attr:attr
						}
					}).then((data)=>{
						if(data.status==200){
							this.total=data.data[0]['COUNT(ID)']
							resolve(this.total)
						}
					})
				})
				
			},
			query_list:function(value){/**/
				return new Promise((resolve)=>{
					this.$http({
						url:`${this.$store.state.query_host}/ApiQueryList`,/**/
						method:'get',
						params:{
							
							data:value
						}
					}).then((data)=>{
						if(data.status==200){
							this.list_data=data.data;
							/*console.log(this.list_data)*/
							resolve(this.list_data)

						}
					})
				})
				
			},



			

		},
		mounted:function(){
			/*console.log('1')*/

			if(this.$route.query.id){/*id*/
				this.initialization(this.$route.query.id)
			}
			
		}
	}

</script>
<style scoped>
	.selector{
		margin-top: 65px;
		
		height: auto;
		width: 100%;

	}
	#main{
		box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
		
		
		width: 80%;
		margin-left: 10%;
	}
	#hardware{
		width: 100%;
		height: auto;
		
	}
	.hardware_head{
		width: 100%;
	  	height: 60px
	}
	.card{
		
		width: 100%;
		
	}
	.pagination{
		
		height: 50px;
		background-color: #f3f3f3;
		padding-bottom: 10px;
		width: 99%;
		padding-right: 1%;
	}
	.menu{
		top: 0px;
	}
	@media (max-width: 992px){
		.selector{
			width: 100%;
			margin-left: 0px;
			margin-top: 40px;
		}
		#main{
			width: 100%;
			margin-left: 0%;
		}
	}
</style>


